<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Employees</h4></li>
      <li v-for="employee in employees" v-bind:key="employee.id" 
      class="collection-item">
      <div class="chip">{{employee.dept}}</div>
      {{employee.employee_id}}:{{employee.name}}

      <router-link class="secondary-content" 
      v-bind:to="{name:'view-employee', params:{employee_id: employee.employee_id}}">
        <i class="fa fa-eye"></i>
      </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default {
    name: 'dashboard',
    data () {
      return {
        employees: []
      }
    },
    created () {
      db.collection('employee').orderBy('dept') .get().then
      (querySnapshot => {
          // console.log("q : ",querySnapshot)
        querySnapshot.forEach(doc => {
          // console.log(`${doc.id} => ${doc.data().employee_id}`);
          const data = {
            'id': doc.id,
            'dept': doc.data().dept,
            'employee_id': doc.data().employee_id,
            'name': doc.data().name,
            'position': doc.data().position
          }
          this.employees.push(data)
        })
      })
    }
  }
</script>


